@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.page {
  color: #161616;
  scroll-behavior: smooth;
  font-family: Cinzel, serif;
  font-size: 16px;
  font-weight: 400;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__header {
  margin-bottom: 80px;
}

@media (min-width: 744px) and (max-width: 1259px) {
  .page__header {
    margin-bottom: 100px;
  }
}

@media (min-width: 1260px) {
  .page__header {
    margin-bottom: 100px;
  }
}

.page__menu {
  z-index: 1;
  transition: transform .6s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  transition: transform .6s;
  transform: translateX(0%);
}

.page__section {
  margin-bottom: 80px;
}

@media (min-width: 744px) and (max-width: 1259px) {
  .page__section {
    margin-bottom: 100px;
  }
}

@media (min-width: 1260px) {
  .page__section {
    margin-bottom: 100px;
  }
}

.page__section-title {
  letter-spacing: -1.5px;
  text-align: center;
  margin: 0 0 48px;
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
}

.page__section-subtitle {
  letter-spacing: normal;
  text-align: center;
  margin: -48px 0 48px;
  padding-top: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
}

.container {
  max-width: 1020px;
  margin: 0 auto;
  padding: 20px 20px 0;
}

@media (min-width: 744px) and (max-width: 1259px) {
  .container {
    padding: 40px 72px 0;
  }
}

@media (min-width: 1260px) {
  .container {
    padding: 40px 120px 0;
  }
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  display: grid;
}

@media (min-width: 744px) and (max-width: 1259px) {
  .grid {
    grid-template-columns: repeat(6, 1fr);
    gap: 24px;
  }
}

@media (min-width: 1260px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    gap: 24px;
  }
}

.grid--responsive {
  display: block;
}

@media (min-width: 744px) and (max-width: 1259px) {
